import { Injectable } from '@angular/core';
import * as PouchDB from 'pouchdb';
import { databaseName } from '../constants';
@Injectable()
export class PouchdbService {

  private _isInstantiated: boolean = false;
  private _db: PouchDB.Database<any>;

  get isInstantiated(): boolean { return this._isInstantiated }
  get db(): PouchDB.Database<any> {
    return this._db;
  }
  constructor() {
    if (!this._isInstantiated) {
      this._db = new PouchDB(databaseName);
      this._isInstantiated = true;
    }
  }

}



