import { Injectable } from '@angular/core';
import { databaseName } from '../shared/constants';
import { PouchdbService } from '../shared/services/pouchdb.service';
import * as PouchDb from 'pouchdb';
import { Journey } from "./journey";
@Injectable()
export class JourneyService {
  
  constructor(private _dbService: PouchdbService) {
    
   }

  saveJourney(journey:Journey): Promise<PouchDB.Core.Response> {
    return this._dbService.db.get(journey.id).then(result => {
      console.log(result);
      let journey = new Journey();
      journey._rev = result._rev;
      journey._id = result._id;
      return this._dbService.db.put(journey);
    }, error => {
      if (error.status === 404) {
        journey._id = journey.id;
        return this._dbService.db.put(journey);
      } else {
        return new Promise((resolve, reject) => reject(error));
      }
    });

  }

}



