import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {MdButtonModule,MdInputModule,MdGridListModule,MdIconModule,MdRadioModule}  from '@angular/material';
import {BrowserAnimationsModule}  from '@angular/platform-browser/animations';
import {FormsModule} from '@angular/forms';

import {OnboardRoutesModule} from  './onboard-routes/onboard-routes.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { JourneyComponent } from './journey/journey.component';
import {UuidService } from './shared/services/uuid.service';
import {PouchdbService } from './shared/services/pouchdb.service';
import { StepsComponent } from './steps/steps.component';
import { StepComponent } from './step/step.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    JourneyComponent,
    StepsComponent,
    StepComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MdButtonModule,
    MdInputModule,
    MdGridListModule,
    FormsModule,
    MdIconModule,
    MdRadioModule,
    OnboardRoutesModule
  ],
  providers:[UuidService,PouchdbService],
  bootstrap: [AppComponent]
})
export class AppModule { }



